.container {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-auto-rows: 200px;
  grid-gap: 5px;
  width: 98vw;
  margin: auto;
}
.gridBlock {
  border: 1px solid #444;
}
.clickFocus {
  border: 0.1em solid #444;
}
.imageWrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.mobileOnly {
  display: none;
}
.desktopOnly {
  display: block;
}
@media only screen and (max-width: 767px) {
  .desktopOnly {
    display: none;
  }
  .mobileOnly {
    display: block;
  }
  .container {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-auto-rows: 100px;
    grid-gap: 5px;
  }
}



